import { setAttribute, setComponentProps, createComponent } from "./index.js"

export function diff( dom, vnode, container ){
    // å¯¹æ¯”èŠ‚ç‚¹å˜åŒ–
    const ret = diffNode( dom, vnode )

    if( container ){
        container.appendChild(  ret )
    }

    return ret
}

export function diffNode( dom, vnode ){
    let out = dom
    if( vnode === undefined || vnode === null || typeof vnode === "boolean" ) return
    // æ•°å­—è½¬æ¢æˆ å­—ç¬¦ä¸²
    if( typeof vnode === "number" ) vnode = String( vnode )
    // å¦‚æœ vnode æ˜¯å­—ç¬¦ä¸²ï¼Œç›´æ¥æ¸²æŸ“
    if( typeof vnode === "string" ){
        // åˆ›å»ºæ–‡æœ¬èŠ‚ç‚¹
        if( dom && dom.nodeType === 3 ){
            if( dom.textContent !== vnode ){
                // æ›´æ–°æ–‡æœ¬å†…å®¹
                dom.textContent = vnode
            }
        } else {
            out = document.createTextNode( vnode )
            if( dom && dom.parentNode ){
                dom.parentNode.replaceNode( out, dom )
            }
        }
        return out
    }

    // å¦‚æœ tag æ˜¯ç»„ä»¶
    if( typeof vnode.tag === "function" ){
        return diffComponent( out, vnode )
    }

    console.log( "æ‰§è¡Œåˆ°è¿™é‡Œile---" )
    // éæ–‡æœ¬ DOM èŠ‚ç‚¹
    if( !dom ){
        out = document.createElement( vnode.tag )
    }
    diffAttribute( out, vnode )
    // æ¯”è¾ƒå­èŠ‚ç‚¹
    if( vnode.childrens && vnode.childrens.length > 0 || ( out.childNodes && out.childNodes.length > 0 ) ){
        // å¯¹æ¯”ç»„ä»¶ æˆ– å­èŠ‚ç‚¹
        diffChildren( out, vnode.childrens )
    }
    return out
}

function diffComponent( dom, vNode ){
    let comp = dom
    // å¦‚æœç»„ä»¶æ²¡æœ‰å˜åŒ– é‡æ–°è®¾ç½® props
    if( comp && comp.constructor === vNode.tag ){
        // é‡æ–°è®¾ç½® props
        setComponentProps( comp, vNode.attrs )
        // èµ‹å€¼
        dom = comp.base
    } else {
        // ç»„ä»¶å‘ç”Ÿå˜åŒ–
        if( comp ){
            // å…ˆç§»é™¤ç»„ä»¶
            unmountComponent( comp )
            comp = null
        }
        // 1. åˆ›å»ºæ–°çš„èŠ‚ç‚¹
        // 2. è®¾ç½®ç»„ä»¶å±æ€§
        // 3. ç»™å½“å‰ç»„ä»¶æŒ‚è½½ base
        comp = createComponent( vNode.tag, vNode.attrs )
        setComponentProps( comp ,vNode.attrs )
        dom = comp.base
    }
    return dom
}

function unmountComponent( comp ){
    removeNode( comp.base )
}

function removeNode( dom ){
    if( dom && dom.parentNode ){
        dom.parentNode.removeNode( dom )
    }
}

function diffChildren( dom, vChildren ){
    const domChildren = dom.childNodes
    const children = []
    const keyed = {}

    // å°†æœ‰ key çš„èŠ‚ç‚¹ï¼ˆç”¨å¯¹è±¡ä¿å­˜ï¼‰å’Œæ²¡æœ‰ key çš„èŠ‚ç‚¹ï¼ˆç”¨æ•°ç»„ä¿å­˜ï¼‰åˆ†å¼€
    if( domChildren && domChildren.length > 0 ){
        vChildren.forEach( item => {
            if( item.key !== null ){
                keyed[item.key] = item
            } else {
                children.push( item )
            }
        })
    }
    if( vChildren && vChildren.length > 0 ){
        let min = 0
        let childrenLen = vChildren.length;
        console.log( "å¼€å§‹ forEarch:", vChildren );
        [ ...vChildren ].forEach( ( vChild, i ) => {
            const key = vChild.key
            let child
            if( key ){
                if( keyed[key] ){
                    child = keyed[key]
                    keyed[key] = undefined
                }
            } else if( childrenLen > min ){
                for( let j = min; j < childrenLen; j ++ ){
                    let c = children[j]
                    if( c ){
                        child = c
                        children[j] = undefined
                        if( j === childrenLen - 1 ) childrenLen --
                        if( j === min ) min ++
                        break
                    }
                }
            }

            child = diffNode( child, vChild )
            // æ›´æ–° dom
            const f = domChildren && domChildren[i]
            if( child && child !== dom && child !== f ){
                if( !f ){
                    dom.appendChild( child )
                } else if( child === f.nextSibling ){
                    removeNode( f )
                } else {
                    dom.insertBefore( child, f )
                }
            }
        })
    }
}


function diffAttribute( dom, vnode ){
    // ä¿å­˜ä¹‹å‰ dom çš„æ‰€æœ‰å±æ€§
    let oldAttrs = {}
    let newAttrs = vnode.attrs
    // dom æ˜¯åŸæœ‰çš„èŠ‚ç‚¹å¯¹è±¡ vnode æ˜¯è™šæ‹Ÿdom
    const attributes = dom.attributes;

    if( attributes ){
        [...attributes].forEach(element => {
            oldAttrs[element.name] = element.value
        })
    }

    // æ¯”è¾ƒ
    // åŸæ¥æ˜¯å±æ€§è·Ÿæ–°çš„å±æ€§å¯¹æ¯”ï¼Œä¸åœ¨æ–°çš„å±æ€§ä¸­ï¼Œåˆ™å°†å…¶ç§»é™¤æ‰(ç¼–ç¨‹ undefined)
    for( let key in oldAttrs ){
        if( !(key in newAttrs ) ){
            setAttribute( dom, key, undefined )
        }
    }
    // æ›´æ–° å±æ€§
    for( let key in newAttrs ){
        if( oldAttrs[key] !== newAttrs[key] ){
            setAttribute( dom, key, newAttrs[key] )
        }
    }
}



